/* alumni-sans-regular - cyrillic_latin */
@font-face {
  font-display: swap;
  font-family: "Alumni Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/alumni-sans-v16-cyrillic_latin-regular.woff2") format("woff2"),
    url("../fonts/alumni-sans-v16-cyrillic_latin-regular.woff") format("woff");
}
