  .games__image {
    height: 292px;
    overflow: hidden;
  }
}
.games__image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 767px) {
  .games__image img {
    width: 115%;
    height: 115%;
  }
}

.games__title {
  margin: 3px 60px 0 11px;
