  line-height: 96%;
  letter-spacing: 0.03em;
  text-align: center;
}
@media (max-width: 1199px) {
  .games__title {
    margin: 2px 36px 0 5px;
    font-size: 48px;
  }
}

.games__description {
  margin: 16px 0 45px;
  width: 100%;
  font-size: 18px;
  line-height: 150%;
}
@media (max-width: 1199px) {
  .games__description {
    margin: 5px 0 29px;
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  .games__description {
    margin: 4px 0 30px -15px;
