}
@media (max-width: 1199px) {
  .games__when dl:not(:last-child) {
    margin: 0 0 7px;
  }
}
@media (max-width: 767px) {
  .games__when dl:not(:last-child) {
    margin: 0 0 6px;
  }
}
.games__when dl dt {
  margin: 0 0 3px;
